<script>
import { MDCTabBar } from "@material/tab-bar";
import { MDCTabScroller } from "@material/tab-scroller";
import { MDCTabIndicator } from "@material/tab-indicator";
import { MDCTab } from "@material/tab";

export default {
  data() {
    return {
